import React from 'react'
import { Link } from 'react-router-dom'
import { useAllPedidos } from '../../Context/AllPedidosContext'
import { Container, Row, Col } from 'react-bootstrap'
import PedidosPreview from '../../Components/PedidosPreview'
import AccountData from '../../Components/AccountData'
import DescargasCard from '../../Components/DescargasCard'

const Home = () => {
    const { pedidos } = useAllPedidos()

    return (
        <Container as='main' fluid className='home'>
            <div className='mainIntro'>
                <h1>Bienvenido a Pedilo YA 2.0</h1>
                <h4>Monitore los pedidos de todas las reas en tiempo real</h4>
            </div>
            <PedidosPreview pedidos={pedidos} />
            <div className='homeQuote'>
                <hr />
                <h5>Estamos mejorando la dinamica de nuestra comunicacin!</h5>
                <p>
                    A travs de esta plataforma ped diseos, publicaciones en redes, noticias web y hacenos llegar tus novedades
                    para Somos.
                </p>
                <p>
                    <Link to='/editar-descargables'>Carg</Link> los manuales de marca y contenidos web de GCBA.
                </p>
                <p>
                    Edita la informacin del <Link to='/editar-equipo'>Equipo de Comunicacin</Link> del Ministerio.{' '}
                </p>
                <hr />
            </div>
            <Row>
                <Col md={5}>
                    <AccountData hasLink={true} />
                </Col>
                <Col md={4}>
                    <DescargasCard hasLink={true} />
                </Col>
            </Row>
        </Container>
    )
}

export default Home
